body{
  background-color:   #1e1e2f;
  color: #e0e0eb; 
}

img {
  max-width: 100%;
}

.app-wrapper {
  max-width: 1200px;
  margin: 0 auto;

  display: grid;

  grid-template-areas: 
  "h h"
  "n c"
  "f f";

  grid-template-rows: 80px 1fr 40px;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  grid-area: c;
  background-color:  #14141f;
 
}







.footer {
  grid-area: f;
  padding: 10px;
  background-color: #0a0a10;
}







